.header {
    padding-top: 10px;

    .header-logo {
        img {
            max-width: 120px;
            margin: 0;
        }
    }

    .header-links {
        text-align: center;
        font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;

        a {
            display: inline-block;
            text-align: center;
            padding: 0 10px;
            margin: 0;

            color: #666666;
            transition: color 0.2s, background 0.2s;
            text-decoration: none;

            &.header-links-home {
                color: #c9282d;
            }

            img {
                display: block;
                max-height: 30px;
                margin: 10px auto 0;
                transition: opacity 0.2s;
                backface-visibility: hidden;
            }

            &:hover {
                color: #c9282d;

                img {
                    opacity: 0.75;
                }

                span {
                    border-bottom: 1px solid #c9282d;
                }
            }

            &:active,
            &:focus {
                color: white;
                background: #c9282d;
                img {
                    opacity: 1;
                }

                span {
                    border-bottom-color: white;
                }
            }
        }
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        .header-links {
            a {
                margin: 0 5px;

                img {
                    max-height: 40px;
                    margin: 10px auto;
                }
            }
        }
    }

    @include breakup-breakpoint('thinish') {
        // 600px
        .header-logo {
            img {
                max-width: 150px;
            }
        }
    }
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding-top: 30px;
        padding-bottom: 30px;

        .header-links {
            text-align: right;

            a {
                margin: 0 0 0 10px;

                img {
                    max-height: 50px;
                }
            }
        }
    }

    @include breakup-breakpoint('wide') {
        // 600px
        .header-logo {
            img {
                max-width: 214px;
            }
        }
    }
}

#slice-navigation {
    margin-bottom: 40px;
}
