body {
    margin: 0;
    padding: 0 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 1.5;
    color: #333333;
    background-color: #ffffff;
    -webkit-font-smoothing: antialiased;
}

.headline-1, h1 {
    font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-size: 2.5em;
    font-weight: 600;
    line-height: 1.4em;
    margin: 6px 0 14px;
}

.headline-2, h2 {
    font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-size: 2em;
    font-weight: 500;
    line-height: 1.4em;
    margin: 6px 0 8px;
}

.headline-3, h3 {
    font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-size: 1.6em;
    font-weight: 500;
    line-height: 1.3em;
    margin: 6px 0 8px;
}

.headline-4, h4,
h5,
h6 {
    font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-size: 1.2em;
    font-weight: 600;
    line-height: 1.3em;
    margin: 6px 0 8px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: inherit;
    text-rendering: optimizelegibility;
}

h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small {
    font-weight: normal;
    line-height: 1;
    color: #999999;
}

hr {
    color: #e7e7e7;
    height: 1px;
    border: none;
    border-top: 1px solid #e7e7e7;
    margin: 1.5 0;
}

/*stop editors messing with headings*/
h1 a,
h1 a:link,
h2 a,
h2 a:link,
h3 a,
h3 a:link,
h4 a,
h4 a:link,
h5 a,
h5 a:link,
h6 a,
h6 a:link,
h1 strong,
h2 strong,
h3 strong {
    font-weight: normal;
    border-bottom: none;
    text-decoration: none;
}

small {
    font-size: 85%;
}

/*allow editors using the EES to make bold anchors*/
strong,
strong a {
    font-weight: bold;
}

em {
    font-style: italic;
}

cite {
    font-style: normal;
}

.standfirst {
    font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-size: 1.6em;
    font-weight: 300;
    line-height: 1.4em;
    margin: 0 0 10px 0;
    color: #666666;
}

p {
    overflow: hidden;
    margin: 0 0 14px;
}

.note {
    background-color: #ffffcc;
    border: 1px solid #ffe599;
    padding: 10px;
}

ul,
ol {
    margin: 0 0 14px 25px;
    padding: 0;
}

ul ol,
ol ul {
    margin-bottom: 0;
}

ul li,
ol li {
    padding: 0;
    line-height: 20px;
}

ol li {
    list-style: decimal;
    margin: 0 0 0 20px;
}

ol ul li {
    list-style: circle;
}

ul ul {
    margin-bottom: 0;
    list-style: circle;
}

ol ol {
    margin-bottom: 0;
}

dl {
    margin-bottom: 1.5;
}

dt {
    padding: 5px;
    background: #f7f7f7;
    margin-bottom: 10px;
    font-family: Georgia, "Times New Roman", Times, serif;
    font-size: 1.2em;
    line-height: 1.5;
    font-weight: bold;
}

dd {
    margin: 0 0 14px 10px;
    line-height: 1.5;
}

/*A-Z listings*/
#alpha-pagination {
    padding: 10px;
    border: 1px solid #cccccc;
    margin: 0 0 20px 0;
    background: #f7f7f7;
    color: #999;
    font-size: 1.2em;
}

#alpha-pagination:before, #alpha-pagination:after {
    content: " ";
    display: table;
}

#alpha-pagination:after {
    clear: both;
}

#alpha-pagination a:visited {
    color: #b02a1a;
}

#alpha-pagination a:active {
    background: #ef383f;
    color: #fff;
}

#alpha-pagination a:hover,
#alpha-pagination a:focus {
    border-bottom: 1px solid #cccccc;
    background: #b02a1a;
    color: #fff;
}

.odd {
    background: #f7f7f7;
}

#content .alpha li {
    margin: 0;
    list-style: none;
    padding: 10px 5px;
}

.notice-message {
    border: 1px solid #aad4ff;
    background: #e3f0fd;
    padding: 10px;
    margin: 0;
}

acronym,
abbr,
dfn {
    border-bottom: 1px #333333 dotted;
    cursor: help;
}

blockquote {
    font-family: Georgia, "Times New Roman", Times, serif;
    font-style: italic;
    font-size: 1.1em;
    line-height: 1.4em;
    color: #666666;
    padding: 0 0 0 40px;
    margin: 20px 0 20px 0;
    background: transparent url("../i/quote.png") no-repeat left top;
    clear: both;
    border: none;
}

blockquote p {
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.875;
}

blockquote small {
    display: block;
    line-height: 1.5;
    color: #999999;
}

blockquote small:before {
    content: '\2014 \00A0';
}

blockquote.pull-right {
    float: right;
    padding-right: 15px;
    padding-left: 0;
    border-right: 5px solid #eeeeee;
    border-left: 0;
}

blockquote.pull-right p,
blockquote.pull-right small {
    text-align: right;
}

blockquote.pull-right small:before {
    content: '';
}

blockquote.pull-right small:after {
    content: '\00A0 \2014';
}

q:before,
q:after,
blockquote:before,
blockquote:after {
    content: '';
}

address {
    display: block;
    margin-bottom: 1.5;
    font-style: normal;
    line-height: 1.5;
}

img.right {
    margin: 0 0 0 10px;
    float: right;
}

img.left {
    margin: 0 10px 0 0;
    float: left;
}

img.no-border {
    border: none;
}

a img {
    border: none;
}

object,
img {
    margin: 0 0 14px 0;
    max-width: 100%;
}

.pub-date {
    margin: 0;
    font-style: italic;
}

#content.testimonials {
    padding: 0 0 20px 0;
}

#content.standard {
    padding: 0 5px;
}

#content.standard table {
    max-width: 500px;
}

#content.image-gallery {
    padding: 0 0 20px 0;
    width: 780px;
    margin: 0;
}

.js #content .youtube,
.js #content .fms {
    display: none;
}

/*styles for labs charts*/
#labs,
.labs {
    display: none;
}

.js #labs,
.js .labs {
    display: block;
    position: relative;
    background: #fafafa;
    margin: 0 0 10px 0;
}

.js #labs i.loading-fa-icon,
.js .labs i.loading-fa-icon {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    font-size: 36px;
    width: 36px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    margin-top: -18px;
    margin-left: -18px;
    color: #b02a1a;
}

.js #labs p,
.js .labs p {
    padding: 20px;
    display: block;
}

