.filters {
    padding: 0 0 20px;
    text-align: center;

    select {
        padding: 10px;
        color: #888;
    }

    button {
        display: none;
        position: relative;
        top: -1px;

        height: 40px;
        color: #888;
    }
}

.no-js,
.lt-ie9 {
    .filters button {
        display: inline-block;
    }
}

.filter-box__inner {
    background: rgb(77, 42, 66);
    color: white;
    h2 {
        font-size: $font-size-h3;
        &,
        span,
        i {
            line-height: 29px;
        }
    }
}
