.fw-section-wrapper.section-fw-white-wrapper.starhere-home-ribbon-wrapper {
    background: $purple;

    padding: 30px 0;

    .starhere-home-ribbon {
        h2,
        p {
            color: white;
        }

        h2 {
            font-size: 22px;
            font-weight: 500;
        }

        .course-search {
            padding-top: 20px;

            .course-search-textfield-wrapper {
                padding-right: 36px;
                input {
                    width: 100%;
                }
            }

            button {
                float: right;
            }
        }
    }
}

.course-search {
    .course-search-input {
        height: 38px;
        background: #f7f7f7;
        padding: 10px;
        border: none;
        float: left;
        display: inline;
        margin: 0;
        font-size: 1em;
        color: #333;
        z-index: 1001;
        width: 250px;
        -webkit-border-radius: 0;
    }

    .course-search-submit {
        border: none;
        background-color: $grey1;
        cursor: pointer;
        height: 38px;
        width: 36px;
        i {
            color: $link-color;
        }
    }
}
