.home {
    .start-here-home-intro {
        text-align: center;

        h2 {
            font-size: 18px;
            color: #c9282d;
            margin: 0;
            font-weight: 500;
        }
    }

    .banner.banner-start-here {
        position: relative;
    }

    .image-banner#promo-wrapper {
        margin-bottom: 40px;

        .banner {
            margin-bottom: 0;

            img {
                margin: 0;
            }

            .banner-m-w {
                position: absolute;
                left: 0;
                width: 100%;
                bottom: 0;
            }

            .banner-text-wrapper {
                position: relative;
                padding-top: 20px;

                h2 {
                    font-size: 1.4em;
                    font-weight: 500;
                }

                p {
                    font-size: 1.5em;
                    margin: 0;
                    font-weight: 500;
                }

                .banner-text {
                    position: static;
                    margin-top: 10px;
                    background: $purple;
                }

                @include breakup-breakpoint('medium') {
                    // 768px
                    position: absolute;
                    top: 0;
                    width: 100%;
                    height: 100%;

                    .banner-text {
                        display: inline-block;
                        width: auto;
                        padding: 10px;
                    }
                }

                @include breakup-breakpoint('wide') {
                    // 980px
                    p {
                        font-size: 2em;
                    }
                }
            }

            .banner-m-w,
            .line-m-w {
                display: block;
            }
            .banner-d,
            .line-d-w {
                display: none;
            }

            @include breakup-breakpoint('medium') {
                .banner-m-w,
                .line-m-w {
                    display: none;
                }
                .banner-d,
                .line-d-w {
                    display: block;
                }
                .banner-d {
                    margin: auto;
                }
            }

            .line-m-w {
                padding-top: 150px;
                text-align: center;
            }

            .line-m-c {
                margin: 0 auto;
                width: 100%;
                max-width: 400px;
                overflow: hidden;
            }

            .line-m-s {
                position: relative;
                padding-top: 35%;
                transition: padding-top 0.2s ease;

                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: 0;
                }
            }

            .line-m {
                margin-top: 20px;
                width: 400px;
                max-width: 100%;
            }

            .banner-toggle {
                position: absolute;
                bottom: 5px;
                left: 5px;

                width: 30px;
                height: 30px;
                text-align: center;

                border-radius: 15px;
                background: $purple;
                color: white;
                cursor: pointer;

                i {
                    line-height: 30px;
                }

                .fa-angle-up {
                    display: none;
                }

                @include breakup-breakpoint('medium') {
                    // 768px
                    display: none;
                }
            }

            &.open {
                .banner-toggle {
                    .fa-angle-down {
                        display: none;
                    }
                    .fa-angle-up {
                        display: block;
                    }
                }

                .line-m-s {
                    padding-top: 123%;
                }
            }

            .line-d-w {
                float: right;
                width: 460px;

                @include breakup-breakpoint('wide') {
                    // 980px
                    width: 625px;
                    padding-top: 15px;
                }

                img {
                    margin: 0;
                    width: 100%;
                }
            }
        }
    }

    #slice-navigation {
        display: none;
    }
}

.white-box--cards-intro {
    background-color: $purple;
    color: $white;
    h2 {
        font-weight: 300;
        b {
            font-weight: 700;
        }
    }
}
