.teaser {
    text-align: center;
    padding: 20px 0;
    background: mediumpurple;
    color: white;
}

.tube-map {
}

.tube-map-svg-wrapper {
    width: 100%;
    padding-bottom: 75%;
    height: 0;
    position: relative;
}

.tube-map-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    path,
    line,
    circle {
        transition: stroke 0.2s;
    }

    text {
        font-size: 20px;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        fill: #333;
        font-weight: 500;
        stroke: none;
        transition: fill 0.2s, font-size 0.2s, font-weight 0.2s;

        &.selectable {
            cursor: pointer;
            text-decoration: underline;
            &:hover {
                fill: #888;
                font-size: 22px;
            }
        }

        &.selected {
            fill: #b91b1c;
            font-weight: 600;
            font-size: 22px;
        }
    }

    @each $type in $subject-colors {
        .line-#{nth($type, 1)} {
            .line-color {
                stroke: nth($type, 3);
                cursor: pointer;
            }

            &:hover {
                .line-color {
                    stroke: lighten(nth($type, 2), 20%);
                }
            }

            &.selected {
                .line-color {
                    cursor: default;
                    stroke: nth($type, 2);
                }
            }
        }
    }

    .tube-map-thames {
        path {
            fill: none;
            stroke: #50ade5;
            stroke-width: 20px;
            stroke-linejoin: round;
        }

        text {
            fill: white;
            stroke: none;
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-size: 16px;
        }
    }
}

.tube-map-img {
    display: none;
}

@import 'slick/slick';
@import 'slick/slick-theme';

.featured-stops {
    margin-top: 30px;
    background: #3e1031;
    color: white;

    padding: 5px;
    @include breakup-breakpoint('medium') {
        // 768px
        padding: 0;
    }

    .featured-stop {
        position: relative;

        padding: 5px;
        @include breakup-breakpoint('medium') {
            // 768px
            padding: 0;
        }

        .featured-stop-header {
            padding: 0 10px;
            font-weight: 600;
        }

        .featured-stop-text {
            padding: 0 10px;
            margin-bottom: 10px;
        }

        .featured-stop-link {
            padding: 0 10px;
            a {
                color: white;
                text-decoration: none;

                i {
                    margin-left: 5px;
                    font-size: 0.8em;
                }

                span {
                    border-bottom: dotted 1px white;
                }

                &:visited {
                    color: #eee;
                    span {
                        border-bottom: dotted 1px #eee;
                    }
                }

                &:hover {
                    span {
                        border-bottom: solid 1px white;
                    }
                }
                &:focus {
                    color: #3e1031;
                    background: white;
                }
            }
        }
    }

    @each $type in $subject-colors {
        &.#{nth($type, 1)} {
            .featured-stop:before {
                background: darken(nth($type, 2), 10%);
            }
        }
    }
}

.featured-stops-static {
    .featured-stop {
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .featured-stop-media {
        max-width: 50%;
        float: left;
        margin: 0;
        padding: 5px 10px;

        img {
            margin: 0;
        }
    }
}

.js {
    .featured-stops-static {
        display: none;
    }
}
